// Fluid Responsive by Finsweet https://finsweet.com/client-first/docs/fluid-responsive

:root {
  --font-from-0: 12;
  --font-to-0: 16;
  --vw-from-0: calc(1 / 100);
  --vw-to-0: calc(479 / 100);
  --coefficient-0: calc((var(--font-to-0) - var(--font-from-0)) / (var(--vw-to-0) - var(--vw-from-0)));
  --base-0: calc((var(--font-from-0) - var(--vw-from-0) * var(--coefficient-0)) / 16);

  --font-from-1: 12;
  --font-to-1: 17;
  --vw-from-1: calc(479 / 100);
  --vw-to-1: calc(992 / 100);
  --coefficient-1: calc((var(--font-to-1) - var(--font-from-1)) / (var(--vw-to-1) - var(--vw-from-1)));
  --base-1: calc((var(--font-from-1) - var(--vw-from-1) * var(--coefficient-1)) / 16);

  --font-from-2: 11;
  --font-to-2: 17;
  --vw-from-2: calc(992 / 100);
  --vw-to-2: calc(1920 / 100);
  --coefficient-2: calc((var(--font-to-2) - var(--font-from-2)) / (var(--vw-to-2) - var(--vw-from-2)));
  --base-2: calc((var(--font-from-2) - var(--vw-from-2) * var(--coefficient-2)) / 16);

  --font-from-3: 17;
  --font-to-3: 20;
  --vw-from-3: calc(1920 / 100);
  --vw-to-3: calc(2848 / 100);
  --coefficient-3: calc((var(--font-to-3) - var(--font-from-3)) / (var(--vw-to-3) - var(--vw-from-3)));
  --base-3: calc((var(--font-from-3) - var(--vw-from-3) * var(--coefficient-3)) / 16);
}

html {
  font-size: calc(var(--base-3) * 1rem + var(--coefficient-3) * 1vw);
}

@media screen and (max-width: 1920px) {
  html {
    font-size: calc(var(--base-2) * 1rem + var(--coefficient-2) * 1vw);
  }
}

@media screen and (max-width: 992px) {
  html {
    font-size: calc(var(--base-1) * 1rem + var(--coefficient-1) * 1vw);
  }
}

@media screen and (max-width: 479px) {
  html {
    font-size: calc(var(--base-0) * 1rem + var(--coefficient-0) * 1vw);
  }
}

html {
  scroll-behavior: initial;
}

html,
body {
  width: 100%;
  height: auto !important;
  min-height: 100%;

  @media (hover: hover) {
    overscroll-behavior: none;
  }
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

.line {
  overflow: hidden;
  transform-origin: bottom;
}