.link_text {
  transform: translateY(0%) rotate(0deg);
  transition: cubic-bezier(0.2, 1, 0.23, 1) 0.65s;
  white-space: nowrap;
  transition-delay: -0.05s;
  position: relative;
}

.link_text-italic {
  position: absolute;
  top: 0;
  left: 0;
  white-space: nowrap;
  transform: translateY(140%) rotate(10deg);
  transition: cubic-bezier(0.2, 1, 0.23, 1) 0.65s;
  transition-delay: 0s;
}

.link::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transition: transform cubic-bezier(0.2, 1, 0.23, 1) 0.65s;
  transition-delay: 0s;
  transform: scaleX(0);
  transform-origin: left;
  border-bottom: 2px solid currentColor;
}
.link::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transition: transform cubic-bezier(0.2, 1, 0.23, 1) 0.65s;
  transition-delay: 0.15s;
  transform-origin: right;
  border-bottom: 2px solid currentColor;
}

@media (hover: hover) {
  .link:hover::before {
    transition-delay: 0.15s;
    transform: scaleX(1);
  }
}
@media (hover: hover) {
  .link:hover::after {
    transition-delay: 0s;
    transform: scaleX(0);
  }
}
@media (hover: hover) {
  .link:hover .link_text {
    transform: translateY(-140%) rotate(-10deg);
    transition-delay: 0s;
  }
}
@media (hover: hover) {
  .link:hover .link_text-italic {
    transform: translateY(0%) rotate(0deg);
    transition-delay: -0.05s;
  }
}

.mf-cursor::before {
  background-color: rgba(23, 17, 17, 0.25);
  backdrop-filter: blur(16px);
  top: -3.75rem;
  left: -3.75rem;
  height: 7.5rem;
  width: 7.5rem;
  transform: scale(0);
}
.mf-cursor::after {
  content: "";
  background-color: #fff;
  display: block;
  position: absolute;
  top: -2.1875rem;
  left: -2.1875rem;
  height: 4.375rem;
  width: 4.375rem;
  opacity: 0;
  transform: scale(0);
  transition: transform 0.25s ease-in-out, opacity 0.1s;
  mask: url("https://uploads-ssl.webflow.com/63d7d5a6ecc8771098dae145/63f38aafbc9f6fb3cd057650_icon-arrow-right.svg");
  will-change: transform;
}
.mf-cursor.-showcase::before {
  transform: scale(1);
}
.mf-cursor.-showcase::after {
  opacity: 1;
  transform: scale(1);
}

